function Social(...rest){

    const element = rest.map((item, i) => {
        return (
            `
                <div class="name flex-input">
                    <input title="Check to remove icon" class="social-checkbox" id="${item, i}" type="checkbox" name="social">
                    <label for="${item}">${item}:</label>
                    <input placeholder="https://www.${item.toLowerCase()}.com" type="text" id="${item.toLowerCase()}" class="general-input name-input">
                
                </div>
            
            `
        )
    });

    return element.join("");

}

export default Social;