import img from "../images/default-img.png";
import logo from "../images/darkfav.png";


function InputBranch(item, {general, social}){
    
    switch(item.id){
        case "Mobile":
            general.phoneNumber.textContent = item.value;
            break;
        case "Email":
            general.email.textContent = item.value;
            break;
        case "Name":
            general.name.textContent = item.value;
            break;
        case "LastName":  
            general.lastname.textContent = item.value;
            break;
        case "Address":  
            general.address.textContent = item.value;
            break;
        case "Company":  
            general.company.textContent = item.value;
            break;
        case "UrlTitle":  
            let data = item.value.split(",");

            if(data.length >= 1){

                for(let i = 0; i < data.length; i++){
                    general.url[i].textContent = data[i].trim();
                }
            }
            break;
        case "Url":  
            let data2 = item.value.split(",");

            if(data2.length >= 1){

                for(let i = 0; i < data2.length; i++){
                    general.url[i].href = data2[i].trim();
                }
            }
            break;
        case "Image":  
            general.banner.src = item.value;
            break;
        case "Logo":  
            general.thumbnail.src = item.value || logo ;
            break;
        case "facebook":
            social.facebook.href = item.value; 
            break;    
        
        case "twitter":
            social.twitter.href = item.value;
            break;     
        
        case "youtube":
            social.youtube.href = item.value;
            break;     
        
        case "linkedin":
            social.linkedin.href = item.value;
            break;     
        
        case "instagram":
            social.instagram.href = item.value;
            break;     
        
    }
}

export default InputBranch;